$font-family: "Montserrat", "Gotham", Arial, Sans-serif;

html,
body {
  padding: 0;
  margin: 0;
  font: 13px $font-family; }

.form-fields {
  position: fixed;
  z-index: 100;
  top: 0;
  width: 100%;
  height: 120px;
  border-bottom: 1px #efefef solid;
  text-align: center;
  background: #fff;
  .field {
    display: inline-block;
    text-align: left;
    padding: 20px 12px;
    font: 1.2em $font-family;
    font-weight: 400;
    line-height: 30px;
    color: #36424a;
    .textbox {
      padding: 7px 5px;
      width: 340px;
      font: 0.8em $font-family; }
    input[type=file] {
      font: 0.8em $font-family; }
    strong {
      font-weight: 600; } } }

.page {
  position: relative;
  width: 1600px;
  margin: 200px auto 0;
  .container {
    position: relative;
    z-index: 20;
    width: 1000px;
    margin: 0 auto;
    .search {
      float: left;
      width: 328px;
      height: 588px; }
    .banner-text {
      position: relative;
      float: right;
      padding: 54px 0 0 0;
      width: 615px;
      height: 540px;
      cursor: pointer;
      color: #787878;
      h1 {
        padding: 0;
        margin-bottom: 0;
        text-transform: uppercase;
        font-size: 32px;
        font-weight: 600;
        line-height: 32px;
        letter-spacing: 4px;
        -webkit-hyphens: auto;
        hyphens: auto; }
      h2 {
        margin-bottom: 0;
        font-weight: normal;
        font-size: 18px;
        -webkit-hyphens: auto;
        hyphens: auto; }
      h4 {
        position: absolute;
        bottom: 15px;
        font-weight: 300;
        font-size: 11px;
        -webkit-hyphens: auto;
        hyphens: auto; } } } }
#banner-image {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10; }


